<template>
  <div class="box">
    <div class="maintop">
      <img class="wawa1"  src="https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa1.png"/>
      <img @click="rule" class="wawa2" src="../../assets/images/activity/51wawa2.png"/>
    </div>
    <div class="startgame">
      <p @click="start">开始游戏</p>
    </div>
    <div class="rules" v-show="rulesShow">
      <div class="head">
        <a>活动说明</a>
        <img @click="cancel" src="../../assets/images/activity/51wawa3.png"/>
      </div>
      <hr />
      <div class="main">
        <div class="one">
          <div>活动时间</div>
          <p>2018年4月30日-5月6日</p>
        </div>
        <div class="two">
          <div>规则详情</div>
          <ul>
            <li>1 . 新登录疯狂抓抓抓用户都将赠送50钻石</li>
            <li>2 .每人每天5次抓取机会（含好友互相抓取次数），好友每天最高偷取次数5次 </li>
            <li>3 . 邀请好友给娃娃机添加奖品（奖品随机） ，每天被添加5次</li>
            <li>4 . 同一好友可给机器每天增加1次奖品（奖品随机）</li>
            <li>5 . 抓取获得的奖品可直接在疯狂抓抓抓的账号查看，娃娃将以邮寄的方式寄出</li>
            <li>6 . 严禁打扰游戏公平性，一经发现将保留停止活动权限，活动奖品清零处理，情节严重者将被封号处理</li>

          </ul>
        </div>
        <div class="two">
          <div>活动奖品</div>
          <ul>
            <li>1 . 娃娃（时尚娃娃可邮寄）</li>
            <li>2 .钻石（疯狂抓抓抓代币，可免费实景抓娃娃</li>
            <li>3 .高级钻石礼盒（最高可获取666钻） </li>
            <li>4 .免邮券（可在疯狂抓抓内免去邮寄费用）</li>
            <li class="zhu">注：活动奖品可在疯狂抓抓抓“个人中心”模块查看</li>
            <li class="zhu">如若钻石没有加上，请单击个人中心退出登录，重新进入即可</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="modal" v-show="modalShow">
      <div class="modalbody">
        <div class="delete">
          <img @click="del" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <p>{{info}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  function GetRequest() {
    var url = location.search;
    var theRequest = new Object();
    if (url.indexOf("?") != -1) {
      var str = url.substr(1);
      var strs = str.split("&");
      for(var i = 0; i < strs.length; i ++) {
        theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
      }
    }
    return theRequest;
  }
  Request = GetRequest();

  var urlhost = process.env.API_AJAX;
  export default {
    name:"",
    data() {
      return {
        rulesShow:false,
        modalShow:false,
        info:""
      }
    },
    components: {
    },
    created:function () {


    },
    mounted:function () {
      var _this = this;
      var usertoken = getCookie("wawajitoken");
      if(!usertoken){
        var a = window.location.href;
        var b = a.split("#")[1];
        var c = b.split("?")[1];
        if(c == "" || c == null){
          var lacoUrl = encodeURIComponent(window.location.href);
          window.location.href = "http://weixin.fkzzz.cn/user/oauth?url="+lacoUrl;//微信授权登录
        }else{
          var t = c.substring(6,46);
          setCookie("wawajitoken",t,8);
          $.ajax({
            type: 'POST',
            url: urlhost+"userinfo",
            data$Type: "jsonp",
            data: {
              "token":t
            },
            success: function (response) {
              var status = JSON.parse(response).status;
              if(status == 0){
                _this.$alert("登录状态已失效，点击确认重新登录").then(function () {
                  _this.clearAll();
                  setCookie("wawajitoken","",-1);
                  var lacoUrl = encodeURIComponent(window.location.href);
                  window.location.href = "http://weixin.fkzzz.cn/user/oauth?url="+lacoUrl;
                });
              }else{
                var mess = JSON.parse(response).message;
                var data = JSON.parse(mess)
//            alert(data.muser_id)
                setCookie("userId",data.user_id);
                setCookie("name",data.nickname);
                setCookie("muser_id",data.muser_id);
                if(data.img_url == "" || data.img_url == null){
                  setCookie("headimg","defalut");
                }else{
                  setCookie("headimg",data.img_url);
                }

              }

            },
          });
        }
      }else{
        $.ajax({
          type: 'POST',
          url: urlhost+"userinfo",
          data$Type: "jsonp",
          data: {
            "token":usertoken
          },
          success: function (response) {
            var status = JSON.parse(response).status;
            if(status == 0){
              _this.$alert("登录状态已失效，点击确认重新登录").then(function () {
                _this.clearAll();
                setCookie("wawajitoken","",-1);
                var lacoUrl = encodeURIComponent(window.location.href);
                window.location.href = "http://weixin.fkzzz.cn/user/oauth?url="+lacoUrl;
              });
            }else{
              var mess = JSON.parse(response).message;
              var data = JSON.parse(mess);
              setCookie("userId",data.user_id);
              setCookie("name",data.nickname);
              setCookie("muser_id",data.muser_id);
              if(data.img_url == "" || data.img_url == null){
                setCookie("headimg","defalut");
              }else{
                setCookie("headimg",data.img_url);
              }
            }

          },
        });
      }

    },
    methods:{
      rule:function () {
        this.rulesShow = true;
      },
      del:function () {
        this.modalShow = false;
      },
      cancel:function () {
        this.rulesShow = false;
      },
      clearAll:function () {
        var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
        if(keys) {
          for(var i = keys.length; i--;)
            setCookie(keys[i],"",-1);
        }
      },
      start:function () {
        var _this = this;
        var token = getCookie("wawajitoken");
        var userid = getCookie("userId");
        var user_nick = getCookie("name");
        var headimg = getCookie("headimg");
        if(headimg == "defalut"){
          headimg = ""
        }else{
          headimg = getCookie("headimg")
        }

          $.ajax({
            type: 'POST',
            url: urlhost+"active/initactive",
            data$Type: "jsonp",
            data: {
              "user_id":userid,
              "user_nick":user_nick,
              "img_url":headimg
            },
            success: function (response) {
              var status = JSON.parse(response).status;
              var data = JSON.parse(response).message;
              console.log(data)
              if(status == 0){
                _this.$layer.msg("进入房间失败，请重新进入")
              }else if(status == 3){
                _this.modalShow = true;
                _this.info = "活动维护暂停中"
              }else{
                setCookie("gnum",data.gnum)
                setCookie("num",data.num)
                setCookie("tnum",data.tnum)
                setCookie("znum",data.znum)
                _this.$router.push({path:"/activity/activityHome"})
              }

            },
          });

      }

    }
  }
</script>

<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
.box{
  position: fixed;
  top:0;
  width: 100%;
  height: 100%;
  background: #eb6566;
  .maintop{
    width: 100%;
    height: n(773);
    .wawa1{
      width: 100%;
      height: n(773);
    }
    .wawa2{
      width: n(93);
      height: n(37);
      position: absolute;
      top:n(12);
      right:n(10)
    }
  }
  .startgame{
    width: 100%;
    height: n(60);
    p{
      font-size: n(34);
      color: #eb6566;
      margin-left:20%;
      margin-top: n(90);
      -webkit-border-radius:20px;
      -moz-border-radius:20px;
      border-radius:20px;
      width: 60%;
      height: n(60);
      display: inline-flex;justify-content: center;
      align-items: center;
      background: #ffffff;
      box-shadow:  0px 5px 5px 0px #d54745;
    }
  }
  .rules{
    position: absolute;
    top:0;
    z-index: 9998;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
    padding: 20px 10px 0 10px;
    .head{
      width: 100%;
      display: inline-flex;
      justify-content: space-between;
      a{
        color: #ffffff;
        font-size: n(28);
      }
      img{
        width: n(45);
        height: n(45);
      }
    }
    .main{
      margin-top: 10px;
      .one{
        div{
          background: #ffffff;
          width: n(140);
          height: n(40);
          font-size: n(28);
          text-align: center;
          line-height: n(40);
        }
        p{
          margin-top: 10px;
          color: #ffffff;
        }
      }
      .two{
        margin-top: n(40);
        div{
          background: #ffffff;
          width: n(140);
          height: n(40);
          font-size: n(28);
          text-align: center;
          line-height: n(40);
        }
        ul{
          margin-top: n(40);
          li{
            color: #ffffff;
            margin-bottom: 5px;
          }
          .zhu{
            color: red;
            font-size: n(22);
          }
        }
      }
    }
  }
  .modal{
    position: fixed;
    top:0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 99;

    .modalbody{
      width: 70%;
      margin-left: 15%;
      padding: n(50) n(20);
      background: #ffffff;
      -webkit-border-radius:n(20);
      -moz-border-radius:n(20);
      border-radius:n(20);
      position: relative;
      top: n(200);
      .delete{
        position: absolute;
        top:n(-20);
        right: n(-20);
        width: n(80);
        height: n(80);
        -webkit-border-radius:50%;
        -moz-border-radius:50%;
        border-radius:50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffff;
        img{
          width: n(40);
          height: n(40);
        }
      }

      p{
        width: 100%;
        display: inline-flex;
        justify-content: center;
        font-size: n(26);
      }
    }

  }
}

</style>



